}

@media screen and (min-width: 7px) {
  #mobile-nav {
    display: none;
  }

  #desktop-nav {
    display: flex;
  }
}

nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
}

nav ul {
  grid-gap: var(--unit2);
  gap: var(--unit2);
}

nav ul li:last-of-type {
  margin-right: 0 !important;
}

nav ul li:active {
  color: var(--cyan0);
}

.sidenav-hide {
  position: fixed;
  left: 0;
  top: 0;
  color: white;
  transition: all 0.3s;
  -webkit-transform: translate(100vw, -100vh);
          transform: translate(100vw, -100vh);
  will-change: transform;
  opacity: 0;
}

.sidenav-show {
  position: fixed;
  left: 0;
  top: 0;
  color: rgba(255, 255, 255, 1);
  transition: all 0.3s;
  -webkit-transform: translate(0, 0);
          transform: translate(0, 0);
  will-change: transform;
  opacity: 1;
  width: 100vw;
}

.sidenav-hide .overlay {
  height: 100vh;
  width: 100vw;
  background-color: rgba(255, 255, 255, 0);
  opacity: 0;
  transition: all 0.3s ease-out;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 0;
  border: none;
  overflow-y: scroll;
}

.sidenav-show .overlay {
  height: 100vh;
  width: 100vw;
  background-color: rgba(255, 255, 255, 1);
  opacity: 1;
  z-index: -1;
  border: none;
  display: flex;
  justify-content: center;
}

#mobile-nav-button {
  transition: 0.4s ease-in-out;
  z-index: 2;
}

#mobile-nav-links {
  z-index: 1;
  -webkit-backdrop-filter: blur(10px);
          backdrop-filter: blur(10px);
}

nav .status {
  box-shadow: inset 0 3px 6px var(--transBlueC-10);
  background: var(--transBlueC-05);
  white-space: nowrap;
}

nav .active::after {
  width: 100%;
  content: "";
  height: 5px;
  position: absolute;
  top: calc(100% + 4.25px);
  left: 0;
  background: var(--primary);
  border-radius: 2px;
  display: none;
}

@media screen and (min-width: 768px) {
  nav .active::after {
    display: block;
  }
}

