    outline: none !important;
}

/* Create a custom radio button */
.checkmark {
  position: absolute;
  top: 0;
  left: 0;
  border-radius: 50%;
}

/* When the radio button is checked, add a blue background */
.nucleotide label.radio-container input:checked~.checkmark {
    /* background-color: var(--green0);
      height: 16px;
      width: 16px;
      top: 4.5px;
      left: 5px; */
}

/* Create the indicator (the dot/circle - hidden when not checked) */
.checkmark:after {
  content: "";
  position: absolute;
  display: none;
}

/* Show the indicator (dot/circle) when checked */
.nucleotide label.radio-container input:checked~.checkmark:after {
  display: block;
}

/* Style the indicator (dot/circle) */
.nucleotide label.radio-container .checkmark:after {
  top: 9px;
  left: 9px;
  width: 8px;
  height: 8px;
  border-radius: 50%;
}

