.chatbot-open-toggle {
  position: fixed;
  top: calc(63.99px + var(--unit2));
  right: var(--unit2);
  z-index: 1;
}

.unread-badge {
  position: absolute;
  top: 0;
  left: 0;
  height: calc(var(--unit1) + var(--unit0));
  background-color: var(--red0);
  color: white;
  font-weight: bold;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  aspect-ratio: 1/1;
  -webkit-transform-origin: bottom center;
          transform-origin: bottom center;
  -webkit-animation: newMessageAnimation 0.4s ease-in-out;
          animation: newMessageAnimation 0.4s ease-in-out;
}

@-webkit-keyframes newMessageAnimation {
  0% {
    opacity: 0;
    -webkit-transform: scale(0) translateY(0);
            transform: scale(0) translateY(0);
  }

  50% {
    opacity: 1;
    -webkit-transform: scale(1.1) translateY(-5px);
            transform: scale(1.1) translateY(-5px);
  }

  100% {
    opacity: 1;
    -webkit-transform: scale(1) translateY(0);
            transform: scale(1) translateY(0);
  }
}

@keyframes newMessageAnimation {
  0% {
    opacity: 0;
    -webkit-transform: scale(0) translateY(0);
            transform: scale(0) translateY(0);
  }

  50% {
    opacity: 1;
    -webkit-transform: scale(1.1) translateY(-5px);
            transform: scale(1.1) translateY(-5px);
  }

  100% {
    opacity: 1;
    -webkit-transform: scale(1) translateY(0);
            transform: scale(1) translateY(0);
  }
}

.hidden {
  visibility: hidden;
}

.projects-section {
  min-height: calc(100vh - 63.99px - 125.38px);
}

.projects-section > div > a {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background: var(--transWhite-50);
  padding: var(--unit2);
  color: var(--transBlueC-75);
  border-radius: var(--unit1);
  box-shadow: 0 3px 6px var(--transBlueC-10);
  -webkit-transform: scale(1);
          transform: scale(1);
}

.projects-section > div > a:hover {
  -webkit-transform: scale(1.01);
          transform: scale(1.01);
  background: white;
  color: var(--transBlueC-75);
  text-decoration: none;
}

/* ChuckGPT.css */

#ChuckGPT {
  position: fixed;
  bottom: 0;
  left: 0;
  -webkit-backdrop-filter: blur(10px);
          backdrop-filter: blur(10px);
  background: rgba(204, 233, 243, 0.75);
  border-radius: var(--unit1);
  overflow: hidden;
  height: calc(100% - 63.99px - var(--unit2));
  width: calc(100% - var(--unit2));
  box-shadow: 0 12px 24px var(--transBlueC-25);
  z-index: 1;
  margin: var(--unit1);
  display: flex;
  flex-direction: column;
  -webkit-transform-origin: bottom right;
          transform-origin: bottom right;
  transition: 0.2s ease-in-out;
}

@media screen and (min-width: 768px) {
  #ChuckGPT {
    max-width: var(--unit6);
    right:0;
    margin-left: auto;
  }
}

@media (hover: none) {
  #ChuckGPT {
    max-width: var(--unit6);
    right:0;
    margin-left: auto;
    border-radius: var(--unit2);
  }
}
  

#ChuckGPT #ChuckGPT-header {
  display: flex;
  align-items: center;
  padding: var(--unit1);
  background-color: var(--transWhite-50);
  -webkit-backdrop-filter: blur(3px);
          backdrop-filter: blur(3px);
  border-radius: var(--unit1) var(--unit1) 0 0;
  background: white;
  box-shadow: 0 6px 12px var(--transBlueC-10);
  border-bottom: 1px solid var(--transWhite-50);
  z-index: 1;
}

.message-container {
  margin: 0 0 var(--unit1) 0;
  max-width: 30ch;
  flex-shrink: 1;
}

.message-container.typing-message {
  position: absolute;
  bottom: 100%;
  left: 0;
  margin: 0 auto 0 var(--unit1);
  padding: var(--unit0) 0;
  max-width: 30ch;
  flex-shrink: 1;
}

.message {
  word-wrap: break-word;
  box-shadow: 0 6px 12px var(--transBlueC-10);
  padding: var(--unit0) var(--unit1);
  background: var(--siteBackground);
  color: inherit;
  max-width: 30ch;
  width: auto;
  border-radius: var(--unit0);
}

.message-timestamp {
  display: flex;
  justify-content: flex-start;
  width: 100%;
  padding: 0 var(--unit0);
}

.typing-indicator {
  display: flex;
  justify-content: center;
  align-items: center;
}

.typing-indicator span.dot {
  height: 5px;
  width: 5px;
  background-color: var(--transBlueC-50);
  border-radius: 50%;
  display: inline-block;
  margin: 0 2px;
  opacity: 0;
  -webkit-animation: typing 1.2s infinite;
          animation: typing 1.2s infinite;
}

.typing-indicator span:nth-child(1) {
  -webkit-animation-delay: 0s;
          animation-delay: 0s;
}

.typing-indicator span:nth-child(2) {
  -webkit-animation-delay: 0.2s;
          animation-delay: 0.2s;
}

.typing-indicator span:nth-child(3) {
  -webkit-animation-delay: 0.4s;
          animation-delay: 0.4s;
}

@-webkit-keyframes typing {
  0% {
    opacity: 0;
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }

  50% {
    opacity: 1;
    -webkit-transform: translateY(-5px);
            transform: translateY(-5px);
  }

  100% {
    opacity: 0;
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
}

@keyframes typing {
  0% {
    opacity: 0;
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }

  50% {
    opacity: 1;
    -webkit-transform: translateY(-5px);
            transform: translateY(-5px);
  }

  100% {
    opacity: 0;
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
}

.history-container {
  display: flex;
  flex-direction: column;
  width: 100%;
  flex-grow: 1;
  overflow: auto;
  height:calc(100% - 191px);
  justify-self: stretch;
  padding: var(--unit1);
  padding-bottom: var(--unit3);
  position: relative;
}

.message-container {
  margin-bottom: var(--unit1);
  -webkit-animation: sent 0.2s ease-in-out;
          animation: sent 0.2s ease-in-out;
  -webkit-transform-origin: bottom;
          transform-origin: bottom;
  flex-shrink: 1;
  max-width: 30ch;
}

@-webkit-keyframes sent {

  0% {
    opacity: 0;
    -webkit-transform: translateY(10px) scale(0);
            transform: translateY(10px) scale(0);
  }
  50% {
    opacity: 0.5;
    -webkit-transform: translateY(-5px) scale(1.1);
            transform: translateY(-5px) scale(1.1);
  }
  100% {
    opacity: 1;
    -webkit-transform: translateY(0) scale(1);
            transform: translateY(0) scale(1);
  }
  
}

@keyframes sent {

  0% {
    opacity: 0;
    -webkit-transform: translateY(10px) scale(0);
            transform: translateY(10px) scale(0);
  }
  50% {
    opacity: 0.5;
    -webkit-transform: translateY(-5px) scale(1.1);
            transform: translateY(-5px) scale(1.1);
  }
  100% {
    opacity: 1;
    -webkit-transform: translateY(0) scale(1);
            transform: translateY(0) scale(1);
  }
  
}

.chat-send-button {
  position: absolute;
  margin: 0 var(--unit1) 11px 0;
  border-radius: var(--unit1);
}

.chat-input-container {
  display: flex;
  justify-content: flex-end;
  align-items: flex-end;
  position: -webkit-sticky;
  position: sticky;
  top: 100%;
  width: 100%;
  margin: 0 auto;
  padding: 0 var(--unit1) var(--unit1) var(--unit1);
}

textarea.chat-input {
  padding-right: var(--unit3);
  border-radius: var(--unit1);
}

textarea.chat-input:focus-visible {
    outline: 3px solid var(--primary);
    outline-offset: -2px;
}
